@import '../../scss/abstracts/variables';
@import '../../scss/abstracts/mixins';

.addSong {
    width: 50rem;
    height: 55rem;
    padding: 2rem;
    overflow-y: scroll ;
    background-color: white;
    z-index: 999;
    position: relative;
    @include absCenter;
    // &::-webkit-scrollbar-track {
    //     box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    // }
    &::-webkit-scrollbar {
        width: 0rem;
    }      
    // &::-webkit-scrollbar-thumb {
    //     background-color: darkgrey;
    //     outline: 1px solid slategrey;
    // }
    // &::-webkit-scrollbar-corner {
    //     background: rgba(0,0,0,0);
    //   }
    


@include respon(tab-port){
    width: 60rem;
    min-height: 50rem;
};

@include respon(phone){
    width: 45rem;
    min-height: 50rem;
};

    .MuiInputBase-root {
        width: 80%;
        font-size: 3rem;
        margin-bottom: 1rem;
    }

    &__song {
        padding: .5rem;
        color: black;
        align-items: center;
        cursor: pointer;
        background-color: $color-grey-light-2;
        margin-top: .5rem;

        &:hover {
            background-color: $color-grey-light-1;
        }
    }

    &__close {
        top: 0;
        right: 0;
        padding: 5px;
        margin: 2rem;
        width: 6rem !important;
        height: 6rem !important;
        fill: black !important;
        cursor: pointer;
        position: absolute;

    }

    &__img {
        width: 5rem;
        height: 5rem;
        margin-right: 1.5rem;
    }

    &__title {
        font-size:1.5rem;
        @include respon(phone){
        font-size:1.5rem;  
        };
        
    }


    .is-loading {

        // @include absCenter;
        &__svg {
            // position: absolute;
            @include absCenter;

        }

        &__txt {
            width: fit-content;
            color: $color-primary;
            font-size: 3rem;
            @include absCenter;
            top: 53%;

        }
    }
}